import React, {useState, useEffect} from 'react';
import moment from 'moment';
import Loading from './Loading';
import EmptyOrders from './EmptyOrders';
import './Styles/MyOrders.css';
import axios from 'axios';

function MyOrders() {
    const [orders, setOrders] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(false);
    const hoje = moment();

    function isWarranty(date) {
        console.log();
        let partesData = date.split("-");
        let day = partesData[2];
        let month = partesData[1] - 1;
        let year = partesData[0];
        let data = new Date(year, month, day);
        if(hoje.diff(data, 'days') <= 30) {
            return true;
        } else {
            return false;
        }
    }

    useEffect(() => {
        setLoading(true);

        axios.post('http://'+window.location.hostname+'/api/get-myorders.php', {
            "user_id": sessionStorage.getItem('user_id')
            })
            .then(function (response) {
                let result = response.data;
                if(result.error) {
                    setError(result.error);
                    setLoading(false);
                } else {
                    const orders = result.map(order => {
                        return {
                            id: order.id,
                            date: order.data.split('-').reverse().join('/'),
                            details: order.details,
                            total: parseFloat(order.total),
                            status: order.status,
                            warranty: isWarranty(order.data),
                        }
                    });
                    setLoading(false);
                    setOrders(orders);
                }
            });
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if (loading) {
        return (<Loading/>);
    }
    if (error) {
        return <EmptyOrders/>;
    }
    return (
        <div className="my-orders">
            <table className='table table-bordered'>
                <thead>
                    <tr>
                        <th>Pedido</th>
                        <th>Data</th>
                        <th>Detalhes</th>
                        <th>Status</th>
                        <th>Total</th>
                        <th>Garantia</th>
                    </tr>
                </thead>
                <tbody>
                    {orders.map(order => (
                        <tr key={order.id}>
                            <td>#{order.id}</td>
                            <td>{order.date}</td>
                            <td>{order.details}</td>
                            <td>{order.status}</td>
                            <td>{order.total.toLocaleString('pt-br',{style: 'currency', currency: 'BRL'})}</td>
                            <td>{order.warranty ? "Sim" : "No"}</td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
}
export default MyOrders;